<template>
<!-- Èù¢ÊùøÁõíÂ≠ê -->
  <div class="panelsbox">
    <!-- Â∑¶ËæπÁöÑÊªöÂä® -->
    <cube-scroll class="leftpanels">
      <ul>
        <!-- ÈÅçÂéÜ‚ÄúÂàÜÁ±ªÊ†áÁ≠æÈ°µ‚ÄùÂ∑¶Ëæπ‚ÄúÂàÜÁ±ªÈ°π‚ÄùÁöÑÊï∞ÊçÆÔºålist-Â∑¶ËæπÊ†èÁöÑÊØè‰∏Ä‰∏™ÂàÜÁ±ªÈ°πÔºåtabslabel-Êï¥‰∏™ÂàÜÁ±ªÊ†áÁ≠æÈ°µ -->
        <!-- 1.selectlist(index)ÁÇπÂáªÂ∑¶ËæπÁöÑÂàÜÁ±ªÈ°πÊó∂ÔºåÂ∞ÜÈÄöËøá‚ÄúË∑ØÁî±ÂØºËà™ÁöÑÊñπÂºè‚Äù(Ë∑≥ËΩ¨)ÂëàÁé∞Âè≥ËæπÁöÑÈ°µÈù¢ -->
        <!-- 2.activeÔºåÂΩìÊüê‰∏Ä‰∏™ÂàÜÁ±ªÈ°πÊ†áÁ≠æË¢´Èº†Ê†áÈÄâ‰∏≠(true)Êó∂ÔºåÁ±ªÂêçÂèò‰∏∫activeÔºåÂê¶Âàô:class=''(Êó†Á±ªÂêç) -->
        <li v-for="(list,index) in tabslabel" :key="index" @click="selectlist(index)" :class="list.active?'active':''">
          {{list.label}}
        </li>
      </ul>
    </cube-scroll>
    <!-- Âè≥ËæπÁöÑÊªöÂä® -->
    <cube-scroll class="rightpanels">
      <ul>
        <!-- ÊâÄÈÅçÂéÜÁöÑÊØè‰∏Ä‰∏™li‰∏≠ÁöÑÊØè‰∏Ä‰∏™tagÈÉΩÂØπÂ∫îÁùÄ‰∏Ä‰∏™ÂîØ‰∏ÄÁöÑË¢´Ê†áËØÜÁöÑÁ¥¢Âºïindex -->
        <li v-for="(tag,index) in tags" :key="index">
          <img :src="tag.image" alt="">
          <!-- Âú®ÁÇπÂáª‰∫ã‰ª∂‰∏≠‰º†ÂÖ• $event‰∫ã‰ª∂ÂØπË±° Âíå Êï∞ÊçÆtag ‰∏§‰∏™ÂèÇÊï∞ÔºàÂÆûÁé∞Âä®ÁîªÊïàÊûúË¶ÅÁî®Âà∞‚ùìÔºâ -->
          <p>{{tag.label}} <i class="cubeic-add" @click="addtocart($event,tag)"></i></p>
        </li>
      </ul>
    </cube-scroll>
    <!-- Ê∑ªÂä†ÂïÜÂìÅÊéâÂÖ•Ë¥≠Áâ©ËΩ¶-Âä®Áîª -->
    <div class="ball-wrap">
      <transition
        @before-enter="beforeEnter"
        @enter="enter"
        @afterEnter="afterEnter"
      >
        <div class="ball" v-if="ball.show">
          <div class="inner">
            <i class="cubeic-add">

            </i>
          </div>
        </div> 
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        ball:{
          //ÁêÉÂàöÂºÄÂßã‰∏çÊòæÁ§∫
          show:false,
          //Ëé∑ÂèñÁêÉÁöÑDOM
          el:''
        },
        tags:[],
        tabslabel:[
          {
              label:'ÁÉ≠Èó®Êé®Ëçê',
              active:true
          },
          {
              label:'ÊâãÊú∫Êï∞Á†Å',
              active:false
          },
          {
              label:'ÂÆ∂Áî®ÁîµÂô®',
              active:false
          },
          {
              label:'ÁîµËÑëÂäûÂÖ¨',
              active:false
          },
          {
              label:'ÂÆ∂Â±ÖÂé®ÂÖ∑',
              active:false
          },
          {
              label:'ÂÆ∂ÂÖ∑ÂÆ∂Ë£Ö',
              active:false
          },
          {
              label:'ÁæéÂ¶ÜÊä§ËÇ§',
              active:false
          },
          {
              label:'ÂåªËçØ‰øùÂÅ•',
              active:false
          },
          {
              label:'ÈíüË°®Áè†ÂÆù',
              active:false
          },
          {
              label:'ËÆ°ÁîüÊÉÖË∂£',
              active:false
          },
          {
              label:'ÁîüÊ¥ªÊóÖË°å',
              active:false
          },
          {
              label:'ÂõΩÈôÖÂêçÁâå',
              active:false
          },
        ]
      }
    },
    methods: {
      //ÁÇπÂáªÂ∑¶‰æßÂàÜÁ±ª
      selectlist(index){
        //ind-forEachÂú®ÈÅçÂéÜÊï∞ÁªÑÊó∂‰∏∫ÊØè‰∏Ä‰∏™ÂÖÉÁ¥†ÊâÄÊåáÂÆöÁöÑÁ¥¢Âºï
        this.tabslabel.forEach((element,ind) => {
          //Â∞ÜforEachÊâÄÈÅçÂéÜÂà∞ÁöÑÂàÜÁ±ªÊ†áÁ≠æ‰∏≠ÊØè‰∏Ä‰∏™Ë¢´ÊåáÂÆö‰∫ÜindÁöÑÊ†áÁ≠æÂàÜÂà´‰∏Ä‰∏™‰∏Ä‰∏™Âú∞‰∏éÂΩìÂâçÈº†Ê†áÊâÄÁÇπÂáªÁöÑÈÄöËøáv-forÊâÄÈÅçÂéÜÂá∫Êù•ÁöÑË¢´ÊåáÂÆö‰∫ÜindexÁöÑÊ†áÁ≠æÂÅöÊØîËæÉÔºàÂ§öÂØπ‰∏ÄÊØîËæÉÔºâ
          if(index == ind){
            //Â¶ÇÊûúÂΩìÂâçÊâÄÂ§ÑÁêÜÁöÑÂÖÉÁ¥†ÁöÑÁ¥¢Âºïind == ÂΩìÂâçÊâÄÁÇπÂáªÁöÑliÊ†áÁ≠æÊâÄÂØπÂ∫îÁöÑÁ¥¢ÂºïÂÄºindexÔºåËÆæÁΩÆËØ•ÂÖÉÁ¥†active‰∏∫true(.activeÊ†∑ÂºèÁîüÊïà)
            element.active = true
            //ÂêåÊó∂ÔºåË∞ÉÁî®getclassify()ÊñπÊ≥ï‚ÄúÂèëÈÄÅaxiosÁΩëÁªúËØ∑Ê±Ç‚ÄùËé∑ÂèñÂΩìÂâçÈº†Ê†áÊâÄÈÄâ‰∏≠Ê†áÁ≠æÂØπÂ∫îÁöÑÈ°µÈù¢ÂÜÖÂÆπ
            this.getclassify(index)
          }else{
            //Âê¶ÂàôÔºåËÆæÁΩÆËØ•ÂÖÉÁ¥†active‰∏∫false(.activeÊ†∑Âºè‰∏çÁîüÊïà)
            element.active = false
          }
        })
      },

      //Ëé∑ÂèñÂàÜÁ±ªÁöÑÊñπÊ≥ï
      async getclassify(index){
        //ÂêëÂàÜÁ±ªÈ°µÁöÑÂàÜÁ±ªÊé•Âè£ÂèëÈÄÅaxiosÁΩëÁªúËØ∑Ê±ÇÔºåÂπ∂Â∞ÜËé∑ÂèñÂà∞ÁöÑ‚ÄúÂÄº‚ÄùËµãÂÄºÁªôÂèòÈáèresult
        const result = await this.$http.get('/api/classify',{params:{type:index}})
        //Â∞ÜËé∑ÂèñÂà∞ÁöÑÂÄº‰∏≠ÁöÑdata[¬∑¬∑¬∑]Êï∞ÁªÑÊõ¥Êñ∞ÊõøÊç¢Êéâ‰∏äÈù¢ÁöÑtags:[]
        this.tags = result.data
      },

      //üìåÂ∞ÜÂïÜÂìÅÊï∞ÊçÆÂ≠òÂÖ•vuex‰∏≠ÔºàÂç≥ÔºöÊ∑ªÂä†ÂïÜÂìÅÂà∞Ë¥≠Áâ©ËΩ¶‚ùóÔºâ
      addtocart(e,tag){
        this.$store.commit('tocart',tag)
        //Ê∑ªÂä†ÂïÜÂìÅÁöÑ‰∏ÄÁû¨Èó¥ÔºåËÆ©Â∞èÁêÉÊòæÁ§∫
        this.ball.show = true
        //Ëé∑ÂèñÁÇπÂáªÁöÑÂÖÉÁ¥†e.target
        this.ball.el = e.target
      },

      beforeEnter(el){
            //ËÆ©Â∞èÁêÉÁßªÂä®Âà∞ÁÇπÂáªÁöÑ‰ΩçÁΩÆ
            //Ëé∑ÂèñÁÇπÂáª‰ΩçÁΩÆ
            const dom=this.ball.el
            console.log(dom)
            const rect=dom.getBoundingClientRect()//Ëé∑ÂèñÁÇπÂáªÁöÑdomÁöÑ‰ΩçÁΩÆ
            console.log(rect)
            const x=rect.left-window.innerWidth*0.7
            const y=-(window.innerHeight-rect.top)
            console.log(x,y)
            el.style.display='block'
            el.style.transform=`translate3d(0,${y}px,0)`
            const inner=el.querySelector('.inner')
            inner.style.transform=`translate3d(${x}px,0,0)`
        },
        enter(el,done){
            //Ëß¶ÂèëÈáçÁªò
            document.body.offsetHeight
            //Â∞èÁêÉÁßªÂä®ÂõûÂà∞ÂéüÁÇπÔºåÂ∞±ÊòØË¥≠Áâ©ËΩ¶ÁöÑ‰ΩçÁΩÆ
             el.style.transform=`translate3d(0,0,0)`
             const inner=el.querySelector('.inner')
            inner.style.transform=`translate3d(0,0,0)`
            //ËøáÊ∏°ÂÆåÊàêÂêéÊâßË°åÁöÑ‰∫ã‰ª∂
            el.addEventListener('transitionend',done)
        },
        afterEnter(el){
            //ÁªìÊùüÈöêËóèÂ∞èÁêÉ
            this.ball.show=false
            el.style.display='none'
        }
    },

    //ÂΩìcreatedÈí©Â≠êË¢´Ë∞ÉÁî®Êó∂(‰πüÂ∞±ÊòØÊï∞ÊçÆÂàùÂßãÂåñÊó∂)ÔºåË∞ÉÁî®üëÜÁöÑgetclassify(index)ÊñπÊ≥ïÂêëÊé•Âè£ÂèëÈÄÅÁΩëÁªúËØ∑Ê±Ç
    created() {
      //Ê†πÊçÆÁ¥¢ÂºïÂÄºindexËé∑ÂèñÈªòËÆ§ÁöÑÂàÜÁ±ªÊï∞ÊçÆ
      this.getclassify(0)
    },

    //ÊåÇËΩΩ‰πãÂêéÔºåÂèØ‰ª•ËøõË°å‰∏ÄÁ≥ªÂàóÁöÑDOMÊìç‰Ωú‰∫Ü
    mounted() {
      const leftpanels = document.querySelector('.leftpanels')
      const rightpanels = document.querySelector('.rightpanels')
      //document.documentElement.clientHeight -> Ëé∑ÂèñÂΩìÂâçÂ∑≤ÊåÇËΩΩÁöÑÈ°µÈù¢ÁöÑ‚ÄúÂèØËßÜÈ´òÂ∫¶‚ÄùÔºà‰πüÂ∞±ÊòØÊâãÊú∫Â±èÂπïÁöÑÈ´òÂ∫¶Ôºâ
      const bodyheight = document.documentElement.clientHeight
      //ËÆæÁΩÆÂ∑¶ËæπÁöÑcube-scrollÁöÑÈ´òÂ∫¶
      leftpanels.style.height = bodyheight - 57 + 'px'
      //ËÆæÁΩÆÂè≥ËæπÁöÑcube-scrollÁöÑÈ´òÂ∫¶
      rightpanels.style.height = bodyheight - 57 + 'px'
    },
  }
</script>

<style lang="stylus" scoped>
  .panelsbox
    display flex
    .leftpanels
      width 30%
      li
        height 50px
        line-height 50px
        border-bottom 1px solid #fff
        color #333
        background #f8f8f8
        font-size 14px
      .active
        background #fff
        color #CD5C5C
  .rightpanels
    width 70%
    ul
      display flex
      flex-wrap  wrap
      li
        width 50%
        justify-content center
        align-items center
        font-size 15px
        img 
            width 80px
            height  80px
          .cubeic-add
            font-size 18px
  .ball-wrap
    .ball
      position fixed
      left 70%
      bottom 10px
      z-index 1003
      color red
      transition all 1s cubic-bezier(0.49,-0.29,0.75,0.41)
      .inner
        width 16px
        height 16px
        transition all 1s linear  
</style>
